body {
  min-height: 100vh;
  background-color: #e9e6f2;
  padding: 20px;
  color: #4d457b;
  font-family: "Roboto", sans-serif;
}

h2 {
  font-size: 2rem;
  text-align: center;
  margin-bottom: 15px;
}
h3 {
  font-size: 1.375rem;
}

h3 {
  text-align: center;
}

label,
p {
  margin: 0;
}

%stretch-panel {
  flex-grow: 1;
  margin: 10px;
  padding: 20px;
  border-radius: 20px;
  background-color: white;
}

input[type="checkbox"] {
  display: none;
}

input[type="checkbox"] + p {
  position: relative;
  padding-left: 35px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

input[type="checkbox"] + p:before {
  content: "";
  display: block;
  width: 20px;
  height: 20px;
  border: 1px solid;
  border-radius: 5px;
  position: absolute;
  left: 0;
  top: calc(50% - 10px);
  opacity: 0.6;
  -webkit-transition: all 0.12s, border-color 0.08s;
  transition: all 0.12s, border-color 0.08s;
}

input[type="checkbox"]:checked + p:before {
  width: 10px;
  top: -6px;
  left: 8px;
  border-radius: 0;
  opacity: 1;
  border-top-color: transparent;
  border-left-color: transparent;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

button {
  cursor: pointer;
  padding: 6px 10px;
  min-width: 120px;
  height: 32px;
  border-radius: 5px;
  font-weight: bold;
  background-color: #6f6897;
  border: 0;
  color: white;
  transition: background-color 0.12s;
  font-size: inherit;

  &:hover {
    background-color: #4d457b;
  }
}

.App {
  &__columns {
    display: flex;
    justify-content: stretch;
    align-content: stretch;
  }
  &__sidebar {
    @extend %stretch-panel;

    flex-basis: 60%;
  }

  &__content {
    @extend %stretch-panel;

    flex-basis: 30%;
    position: relative;
  }

  &__content-container {
    position: sticky;
    top: 20px;
  }

  @media screen and (min-width: 991px) {
    max-width: 910px;
    margin: 0 auto;
  }

  @media screen and (max-width: 767px) {
    &__columns {
      flex-direction: column-reverse;
    }
  }
}

.warning, .info {
  font-style: italic;
  line-height: 1.3;
}
.warning {
  color: #c72d2d;
}
.info {
  color: #777;
}

.pl-30 {
  padding-left: 30px;
}
